@use "../abstracts" as ab;

* {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: url("../../assets/lyrics-bg.png") center center;

  @include ab.respond-to("small") {
    height: 124vh;
  }
}
